@import './../../../styles';

.footer_links {
  display: grid;
  row-gap: 16px;

  @include typographyUpperCase;
  color: $secondary-color;

  @include onTablet {
    display: flex;
    gap: 13.5px;
  }

  @include onDesktop {
    gap: 106px;
  }

  &__item {
    display: flex;
    align-items: center;
    @include hover(color, $primary-color);
  }
}